* {
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box;

}


.container {

    color: #ffffff;
    max-width: 700px;
    width: 100%;
    position: relative;
    box-shadow: '0px 8px 16px rgba(0, 0, 0, 0.2)';

}

form label {

    font-size: 1rem;
}

.task-heading {
    margin: 2rem;
    text-align: center;
    margin-bottom: 5rem;
}

form {

    max-width: 700px;
}

input {

    width: 100%;

    margin: 1rem auto;
    outline: none;
    padding: .8rem;
    border-radius: .2rem;
    color: gray;
}


.addbtn {
    margin-top: 1rem;
    padding: .3rem;
    background-color: #ffbf00;
    color: #1b1b32;
    font-weight: 300;
    font-size: 1rem;
    outline: none;
    border-radius: 4px;
    position: absolute;
    right: 0;
    top: 2.5rem;
    margin-left: 1rem;

}

.save {

    margin-top: 1rem;

    padding: .3rem;
    background-color: #ffbf00;
    color: #1b1b32;
    font-weight: 300;
    font-size: 1rem;
    outline: none;
    border-radius: 4px;

}


input::placeholder {
    color: #1b1b32;
}



input:focus {
    outline: none;
}

.itemContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    color: #2d2c2c;
    border-radius: .4rem;
    padding: .5rem;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    height: fit-content;
    margin: 1rem auto;

}

button {
    border: none;
}

.itemname {
    font-size: 1rem;
}

.itemdesc {
    margin-top: 1rem;
    font-size: 1rem;
}

.deletebtn {
    background-color: #1b1b32;
    color: #ffff;
    border-radius: 4px;
    margin: .4rem;
    padding: .4rem .6rem;
    outline: none;
    font-size: 1rem;
}

.editbtn {
    background-color: #ffbf00;
    color: #1b1b32;
    border-radius: 4px;
    font-size: 1rem;
    margin: .4rem;
    padding: .4rem .6rem;
    outline: none;
}

@media screen and (min-width: 761px) {

    .container {
        margin: 1px auto;
    }

    .itemContainer {
        margin: 20px auto;
    }

    form label {
        margin-left: 1rem;
    }


@media screen and (max-width: 760px) {
    input {
        width: 100%;
    }

    .task-heading {
        font-size: 1.5rem;
    }

    .itemdesc {
        font-size: .8rem;
    }

    .itemname {
        font-size: .9rem;
    }

    .editbtn,
    .deletebtn {
        font-size: .8rem;
    }

    .container {
        /* margin: auto; */
        width: 99vw;
    }


}

@media screen and (max-width: 760px) {
    .container {

        width: 99vw;
        margin: .1rem;
    }



    .form {
        width: 99vw;
    }

    .save {
        margin-bottom: 1rem;
    }

    .itemContainer {
        width: 99vw;
        margin: 2rem auto;
    }
}}